<template>
    <div>
        <div class="modal" id="editClient" aria-labelledby="editClientLabel" aria-hidden="true"
            :class="{ showModal: openEditModal }">
            <div class="modal-dialog modal-xl">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="editClientLabel">Edit Client</h5>
                        <button type="button" class="btn-close" aria-label="Close" @click.prevent="closeModal()"></button>
                    </div>
                    <div class="modal-body d-grid gap-3" v-if="Object.keys(editClient).length">

                        <div class="row">
                            <div class="col-md-3">
                                <label for="c-name" class="form-label fw-bold">Name:</label>
                            </div>
                            <div class="col-md-9">
                                <input type="text" class="form-control" id="c-name" :value="editClient.name">
                            </div>

                        </div>
                        <div class="row">
                            <div class="col-md-3">
                                <label for="c-email" class="form-label fw-bold">Email </label>
                            </div>
                            <div class="col-md-9">
                                <input type="email" class="form-control" id="c-email" :value="editClient.email">
                            </div>

                        </div>
                        <div class="row">
                            <div class="col-md-3">
                                <label for="c-phone-number" class="form-label fw-bold">Phone Number:</label>
                            </div>
                            <div class="col-md-9">
                                <input type="email" class="form-control" id="c-phone-number"
                                    :value="editClient.phoneNumber">
                            </div>

                        </div>
                        <div class="row">
                            <div class="col-md-3">
                                <label for="c-providers" class="form-label fw-bold">Providers:</label>
                            </div>
                            <div class="col-md-5">
                                <input type="text" class="form-control" id="c-providers">
                            </div>
                            <div class="col-md-4">
                                <button class="btn btn-default"> Add Provider</button>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-3">
                                <!-- <label for="c-providers" class="form-label">Providers</label> -->
                            </div>
                            <div class="col-md-9">
                                <div class="row" v-for="(item, index) in providers" :key="index">
                                    <div class="col-md-7">
                                        <div class="form-check">
                                            <input class="form-check-input" type="checkbox" :value="{ id: item._id }"
                                                :id="item._id" v-model="editClient.providers">
                                            <label class="form-check-label" :for="item._id">
                                                {{ item.name }}
                                            </label>

                                        </div>
                                    </div>
                                    <div class="col-md-1">
                                        <a href="">
                                            <i class="fa fa-edit" aria-hidden="true"></i>
                                        </a>
                                    </div>
                                    <div class="col-md-1">
                                        <a href="">
                                            <i class="fa fa-trash" aria-hidden="true"></i>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <!-- <button type="button" class="btn btn-danger">Delete</button> -->
                        <button type="button" class="btn btn-secondary" @click.prevent="closeModal()">Cancel</button>
                        <button type="button" class="btn btn-primary" @click="updateClient()">Edit Client</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
    name: "ClientEdit",
    computed: {
        ...mapGetters([
            'editClient',
            'openEditModal',
            'providers'
        ])
    },
    methods: {
        closeModal() {
            this.$store.commit("openCloseEditModal", false);
        },
        updateClient() {
            this.$store.dispatch("editClient", this.editClient);
        }
    }
}

</script>
<style>
.showModal {
    display: block !important;
}
</style>